.headerModel_swiper-container{
    position: relative;
    height: 440px;
}

.headerCar_swiper-container{
    position: relative;
    min-height: 630px;
}

.headerModel_swiper {
    width: 100%;
    position: absolute;
}

.headerModel_swiper-slide {
    height: 440px;
    object-fit: cover;
}

.headerModel_swiper-slide img {
    height: 100%;
}

.headerModel_swiper .slick-dots {
    bottom: 1.5rem;
}


.headerModel_swiper .slick-dots.slick-thumb li,
.headerModel_swiper .slick-dots.slick-thumb li .dot {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: transparent;
}

.headerModel_swiper .slick-dots.slick-thumb li {
    background-color: transparent;
}

.headerModel_swiper .slick-dots.slick-thumb li.slick-active {
    position: relative;
}

.headerModel_swiper .slick-dots.slick-thumb li.slick-active {
    position: relative;
    background-color: var(--color-h);
    transition-duration: 0.8s;
    transition-property: transform;
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
}

.headerModel_swiper .slick-dots.slick-thumb li.slick-active:after {
    content: '';
    position: absolute;
    background-image: url('../../assets/HeaderHomeSliderActiveDot.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
}

.headerModel_swiper-slide-small{
    padding: 1rem 0.5rem;
}

@media screen and (max-width: 650px) {
    .headerCar_swiper-container{
        min-height: 520px;
    }

    .swiperType1-slide {
        height: 330px;
    }
}