.headerModel {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.headerCar{
    display: grid;
    grid-template-columns: 1fr 80px 1fr;
    gap: 2rem;
}

.headerModel-head_tags {
    display: flex;
    gap: 1rem;
    align-items: center;
    padding-bottom: 0.5rem;
}

.headerModel-head_tags p:first-of-type {
    color: white;
    background-color: var(--color-accent);
    border: 1px solid var(--color-accent);
    padding: 0 0.5rem;
}

.headerModel-head_tags p:last-of-type {
    border: 1px solid var(--color-h);
    padding: 0 0.5rem;
}

.headerModel__cutoffText {
    --max-lines: 5;
    --line-height: 1.5;
    line-height: var(--line-height);
    max-height: calc(var(--max-lines)*1em*var(--line-height));
    overflow: hidden;
    position: relative;
}

.headerModel__cutoffText::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: calc(1.5em * var(--line-height));
    pointer-events: none;
    background: linear-gradient(to bottom, transparent, white);
}

#expandText-headerModel {
    display: none;
}

.headerModel label {
    display: inline-block;
    cursor: pointer;
}

#expandText-headerModel:checked~.headerModel__cutoffText {
    max-height: fit-content;
}

#expandText-headerModel:checked~.headerModel__cutoffText::before {
    display: none;
}

.headerModel-info div {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
}

.headerModel-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: absolute;
    width: 100%;
}

.headerModel-head_catch {
    padding: 0.5rem 1rem;
    background-color: var(--color-accent);
    color: white;
}

.headerModel_footLink-accent,
.headerModel_footLink-black {
    display: block;
    padding: 1.5rem 0;
    text-align: center;
    color: white;
    background-color: var(--color-h);
    margin-bottom: 1rem;
}

.headerModel_footLink-accent {
    background-color: var(--color-accent);
    border: 1px solid var(--color-accent);
}

.headerModel_Car-characteristics div{
    padding: 1rem 0;
    border-bottom: 1px solid var(--color-border-light);
}



@media screen and (max-width: 1200px) {
  
    .headerCar{
        grid-template-columns: 1fr;
    }

  
}
@media screen and (max-width: 1000px) {
  
    .headerModel {
        grid-template-columns: 1fr;
    }
    .headerModel_Car-characteristics{
        display: flex;
        justify-content: space-between;
        gap: 1rem;
        border-bottom: 1px solid var(--color-border-light);
    }
    .headerModel_Car-characteristics div{
        border-bottom:none;
    }
}

