.modifGrid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding: 1rem 0;
    align-items: center;
}

.modifContainer {
    border-bottom: 3px solid var(--color-accent);
   
}

.modifConfig__container {
    border-bottom: 1px solid var(--color-text);
}

.modifBtn {
    text-align: center;
    padding: 1rem;
    background-color: var(--color-accent);
    color: white;
    border: 1px solid var(--color-accent);
}

.modifDropdown{
padding-bottom: 1rem;
}

.modifDescription {
    display: grid;
    grid-template-columns: 1fr 1fr;

}

.modifDescription ul {

    list-style: none;
    padding: 0.5rem 0;
}

.modifDescription ul li {
    padding: 0.2rem;
}

.modifDescription ul li:before {
    content: "• ";
    font-size: 12pt;
}


.configForm {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1rem;
    padding: 1rem 0;
    max-width: 60%;
}

.configForm button {
    width: 100%;
    height: 100%;
    background-color: var(--color-accent);
    border: 1px solid var(--color-accent);
    cursor: pointer;
}

.configForm button h4 {
    color: white;
}

.configDropdownBtn{
    display: flex;
    align-items: center;
    gap: 0.3rem;
    cursor: pointer;
}
.modifBigScreen_textleft{
    text-align: center;
}

@media screen and (max-width: 1400px) {
    .configForm {
   
        max-width: 90%;
    }
    
}

 @media screen and (max-width: 1000px) {
    .configForm {
        grid-template-columns: 1fr;
    }

    .configForm button {
        padding: 1rem 0;
    }

    .modifBigScreen{
        display: none;
    }

    .modifGrid {
        grid-template-columns: 1fr;
    }
    .modifBigScreen_textleft{
        text-align: left;
        padding: 0.2rem 0;
    }
    .modifBtn{
        width: fit-content;
        padding: 1rem 4rem;
    }

    .modifConfig__container{
        padding: 0 1rem;
        margin-top: 1rem;
        border: 1px solid var(--color-text);
    }
}

@media screen and (max-width: 750px) {
   
.modifDescription {
    display: grid;
    grid-template-columns: 1fr;
}
}