* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  font-family: var(--font-base);
}

a {
  color: unset;
  text-decoration: none;
}

body.active-modal {
  overflow-y: hidden;
}


body::-webkit-scrollbar {
  width: 0px;
}

::-moz-selection {
  /* Code for Firefox */
  background: #D3D3D3;
}

::selection {
  background: #D3D3D3;
}

h1::selection,
h2::selection,
h3::selection,
h4::selection,
h5::selection, 
h6::selection,
.bold::selection,
span::selection,
.underline::selection {
  color: white;
  background: var(--color-accent);
}

h1::-moz-selection,
h2::-moz-selection,
h3::-moz-selection,
h4::-moz-selection,
h5::-moz-selection, 
h6::-moz-selection,
.bold::-moz-selection,
span::-moz-selection,
.underline::-moz-selection  {
  color: white;
  background: var(--color-accent);
}


.App{
  max-width: 2500px;
  margin-right: auto;
  margin-left: auto;
}

.section__padding {
  padding: 2rem 6rem;
}

.section__padding-noRightMargin {
  padding: 0 0 2rem 6rem;
}

.section__padding-noY {
  padding: 0 6rem;
}

.phoneOnly{
  display: none;
}

.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon__container {
  width: 75px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.slide-down {
  -webkit-animation: slide-down  ease-in;
  animation: slide-down  0.4s ease-in;
}

@keyframes slide-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@-webkit-keyframes slide-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@-moz-keyframes slide-down {
  0% {
    opacity: 0;
    -moz-transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -moz-transform: translateY(0);
  }
}

.fade {
  -webkit-animation: fade  ease-in;
  animation: fade  0.4s ease-in;
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-moz-keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 850px) {

  .section__padding {
    padding: 2rem 4rem;
  }
  .section__padding-noY {
    padding: 0 4rem;
  }
  .section__padding-noRightMargin {
    padding: 0 0 4rem 4rem;
  }
}

@media screen and (max-width: 650px) {
  .section__padding {
    padding: 1.5rem;
  }
  .section__padding-noY {
    padding: 0 1.5rem;
  }

  .section__padding-noRightMargin {
    padding: 0 0 1.5rem 1.5rem;
  }
}

@media screen and (max-width: 450px) {
  .phoneOnly{
    display: block;
  }
}