.creditForm {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-column-gap: 1rem;
    margin-top: 1rem;
}

.creditForm__cardCar,
.creditForm__creditForm {
    border: 1px solid var(--color-border-light);
    padding: 2rem;
    margin-bottom: auto;
}

.creditForm__creditForm-CarSelect {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 1rem;
    border-bottom: 1px solid var(--color-border-light);
    padding-bottom: 1rem;
    margin-bottom: 1rem;
}

.creditForm_userData-container {
    display: flex;
    gap: 2rem;
    padding-bottom: 1rem;
}

.creditForm__cardCar h2 {
    padding-bottom: 1rem;
}

.creditForm__cardCar-characteristics {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0.5rem;
}

.creditForm__cardCar-imgContainer {
    display: flex;
    justify-content: center;
}

.creditForm__creditForm-infoField {
    display: flex;
    justify-content: space-between;
    border: 1px solid var(--color-accent);
    padding: 1rem;
    margin: 0.4rem 0;
}

.creditFormShort__cardCar-container {
    padding-top: 1rem;
}

.creditFormShort__cardCar-container ul {
    list-style: none;
}

.creditFormShort__cardCar-container li {
    padding-top: 1rem;
}

.creditForm_phoneOnly {
    display: none;
}

@media screen and (max-width: 1200px) {
    .creditForm {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .creditForm_phoneOnly {
        display: block;
    }

    .creditForm__cardCar {
        display: none;
    }
}

@media screen and (max-width: 550px) {

    .creditForm__creditForm-CarSelect {
        grid-template-columns: 1fr;
    }

    .creditForm_userData-container {
        flex-direction: column;
    }

    .creditForm__cardCar-imgContainer img {
        width: 100%;
    }
}