.modalOverlay{
    position: fixed;
    width: 100%;
    top: 0;
}

.modal_container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center; 
    z-index: 10; 
    position: relative;
}

.modalOverlay__bg{
    position: absolute;
    width: 100%;
    height: 100vh;
    background-color: var(--color-overlay);
}

.modalOverlay__modal{
    position: absolute;
}


.modal {
    display: grid;
    grid-template-columns: 1.35fr 1fr;
    width: 965px;
    background-color: var(--color-text);
}

.modal * {
    color: white;
}

.modal_imgContainer img {
    display: block;
    width: 100%;
}

.modal_userData {
    
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 0.5rem;
}

.modal_userData-container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.modal_userData button {
    background-color: var(--color-accent);
    outline: none;
    border: 1px solid var(--color-accent);
    display: block;
    width: 100%;
    padding: 1rem;
    margin-bottom: 1rem;
    cursor: pointer;
}

.modal_userData button div {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
}

.modal_userData button h4,
.modal_userData button svg {
    color: white;
}


@media screen and (max-width: 1000px) {
    .modal {
        grid-template-columns: 1fr;
        width: 70vw;
    }
}

@media screen and (max-width: 600px) {
    .modal {
        width: 100vw;
    }
}