.btn-type1 {
    display: block;
    border: none;
    cursor: pointer;
  }
  
  .btn-type1-container {
    display: flex;
    align-items: center;
  }
  
  .btn-type1 p {
    align-self: stretch;
    display: inline-block;
    padding: 1.4rem 3rem;
    color: white;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
  }
  
  .btn-type1 .btn-type1-container div {
    padding: 1.4rem 1.6rem;
    align-self: stretch;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  .btn-black div img {
    width: 18px;
  }
  
  .btn-type1-black .btn-type1-container div {
    background-color: var(--color-h);
  }
  
  .btn-type1-black p {
    background-color: var(--color-h-dark);
  }
  
  .btn-type1-orange .btn-type1-container div {
    background-color: var(--color-accent-dark);
  }
  
  .btn-type1-orange p {
    border: 1px solid var(--color-accent);
    background-color: var(--color-accent);
  }

  .btn-type1-black:hover .btn-type1-container div {
    background-color: var(--color-text);
  }
  
  .btn-type1-black:hover p,
  .cardType3-btn:hover,
  .cardType3-btn:hover p,
  .headerModel_footLink-black:hover{
    background-color: var(--color-text-light);
    color: white;
  }
  
  .btn-type1-orange:hover .btn-type1-container div {
    background-color: #f47c36;
  }
  
  .btn-type1-orange:hover p,
  .bannerForm_form-grid button:hover ,
  .bannerForm_form-grid button:hover h4,
  .modifBtn:hover,
  .configForm button:hover,
  .configForm button:hover h4,
  .headerModel_footLink-accent:hover,
  .modal_userData button:hover,
  .modal_userData button:hover h4,
  .modal_userData button:hover svg
  {
    color: var(--color-accent);
    background-color: white;
  }


  .btn-type2 {
    border: none;
    cursor: pointer;
    background-color: transparent;
    opacity: 0.4;
    width: 39px;
  }
  
  .btn-type2:hover {
    opacity: 1;
  }
  